// БЛОК «ABOUT»

/* Основа */

/* ---------- */

/* Первый блок */

.about__info {
	@include row;
	flex-direction: row !important;
}

.about-info__pic {
	margin-bottom: rem(32);
	@include col-12;
	@include smart {
		@include col-4;
		margin-bottom: 0;
		padding-right: rem(16);
	}
	@include tabletm {
		@include col-6;
		padding-right: rem(32);
	}
	@include laptop {
		padding-right: rem(80);
	}
}

.ab-pic-wr {
	height: 100%;
	img {
		width: 100%;
		object-fit: cover;
		height: 100%;
	}
}

.about-info__desc {
	@include col-12;
	@include smart {
		@include col-8;
	}
	@include tabletm {
		@include col-6;
	}
}

.about__info {
	display: flex;
	flex-direction: column;
}

.b3-title {
	margin-bottom: rem(16);
	h2 {
		color: $title;
		margin-bottom: 0;
	}
}

.about-info__text {
	max-width: rem(380);
	& > * {
		margin-bottom: rem(16);
		&:last-child {
			margin-bottom: 0;
		}
	}

	p {
		@include font(14, 18);
	}
}

.about-info__item {
	display: flex;
	flex-direction: row;
	align-items: center;

	.ic {
		min-width: rem(16);
		min-height: rem(16);
		max-width: rem(16);
		max-height: rem(16);
		margin-right: rem(10);
		@include bg;
	}

	p {
		font-weight: 600;
		color: $title;
		@include font(13, 18);
	}
}

/* ---------- */

/* Цифры */

.about__numbers {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: rem(16);
	margin-top: rem(32);
	margin-block-end: rem(32);

	@include smart {
		grid-template-columns: repeat(4, 1fr);
	}

	@include tabletm {
		margin-top: rem(48);
		margin-bottom: rem(48);
	}

	@include tabletb {
		margin-top: rem(80);
		margin-bottom: rem(80);
	}
}

.about-num {
	display: flex;
	flex-direction: column;
	p {
		@include font(24, 64);
		color: $title;
		font-weight: 700;
	}
	span {
		@include font(12, 18);
	}
}

/* ---------- */

/* Команда */

.about__team {
	display: flex;
	flex-direction: column;
}

.ab-t-title {
	display: flex;
	flex-direction: column;
	@include smart {
		flex-direction: row;
		justify-content: space-between;
		h2 {
			@include col-8;
			padding-right: rem(40);
			margin-bottom: 0;
		}

		p {
			@include col-4;
		}
	}
	@include tabletb {
		margin-bottom: 0;
	}
}

.tm-pic-wr {
	margin-bottom: rem(24);
	@include tabletm {
		margin-bottom: rem(48);
	}
}

.ab-t-title {
	margin-bottom: 0;
}

/* ---------- */

// КОНЕЦ БЛОКА «ABOUT» ==========
