// БЛОК «PARTNERS»

/* Основа */

.b4-sct {
	.row {
		border-top: rem(1) solid $border-input;
		border-bottom: rem(1) solid $border-input;
	}
}

/* ---------- */

/* Заголовок */

.prtns-title {
	h2 {
		margin-bottom: 0;
		@include font(40, 56);
	}

	@include laptop {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		margin-bottom: 0;
		@include col-4;
		padding-right: rem(40);
	}
}

/* ---------- */

/* Логотипы */

.partners__logos {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: rem(8);
	@include tabletb {
		grid-template-columns: repeat(4, 1fr);
	}
	@include laptop {
		@include col-8;
	}
}

.partners-item {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: rem(16);
	border: rem(1) solid $border-input;
	border-radius: rem(8);
	@include laptop {
		border: 0;
	}
}

/* ---------- */

// КОНЕЦ БЛОКА «PARTNERS» ==========
