/* Название блока */

.b5-title {
	@include smart {
		@include col-8;
	}
}

.qualities__items {
	display: flex;
	flex-direction: column;

	& > * {
		margin-bottom: rem(16);
		&:last-child {
			margin-bottom: 0;
		}
	}

	@include smart {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: rem(24);
	}

	@include tabletm {
		gap: rem(40);
	}
}

.quality-item {
	display: flex;
	flex-direction: row;

	.pic {
		position: relative;
		top: rem(6);
		min-width: rem(32);
		min-height: rem(32);
		max-width: rem(32);
		max-height: rem(32);
		margin-right: rem(10);
		@include tabletm {
			min-width: rem(128);
			min-height: rem(128);
			max-width: rem(128);
			max-height: rem(128);
			margin-right: rem(32);
		}
		@include laptop {
			min-width: rem(224);
			min-height: rem(224);
			max-width: rem(224);
			max-height: rem(224);
			margin-right: rem(32);
		}
	}
}

.quality__text {
	h4 {
		margin-bottom: rem(8);
	}
	p {
		@include font(13, 16);
	}
}

