// ПОДВАЛ

/* Основной блок */

.ft-sct {
	// overflow: hidden;
	border-top: 1px solid $border-input;
}

.ftpd {
	@include tabletm {
		padding-bottom: 0 !important;
	}
}

.ft-title {
	position: relative;

	&::after {
		display: none;
		position: absolute;
		z-index: -1;
		top: rem(-160);
		opacity: 0.5;
		left: rem(80);
		content: "";
		width: rem(400);
		height: rem(400);
		background-image: url("../img/pic/bg/bg-circle.png");
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;
		@include tabletm {
			display: block;
		}
		@include laptop {
			width: rem(800);
			height: rem(800);
		}
	}

	@include tabletm {
		@include col-8;
		margin-bottom: 0 !important;
	}
}

.footer__text {
	display: flex;
	flex-direction: row;
	max-width: rem(543);
	.ic {
		position: relative;
		top: rem(16);
		width: rem(60);
		height: rem(2);
		background-color: $title;
		margin-right: rem(16);
		@include laptop {
			width: rem(100);
		}
	}
	p {
		@include font(16, 24);
		color: $title;
		font-weight: 600;
	}
}

.footer-btn {
	@include tabletm {
		@include col-4;
		display: flex;
		align-items: center;
		justify-content: flex-end;
	}
}

/* ---------- */

/* Первая строчка футера */

.footer__logo {
	display: flex;
	flex-direction: column;
	@include col-12;
	margin-bottom: rem(16);
	@include tabletb {
		@include col-3;
		padding-right: rem(48);
	}
}

.footer__desc {
	margin-top: rem(8);
	p {
		@include font(11, 16);
		font-weight: 600;
		color: $title;
	}
}

.footer__menu {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: rem(16);
	margin-bottom: rem(16);

	@include smart {
		grid-template-columns: repeat(4, 1fr);
	}

	@include tabletb {
		@include col-9;
	}
}

.menu-col {
	ul {
		li {
			margin-bottom: rem(10);
			a {
				color: $text;
				@include font(12, 16);
				&:hover {
					color: $link-hvr;
				}
			}
		}
		& li:last-child {
			margin-bottom: 0;
		}
	}
}

.menu-tit {
	font-weight: 600;
	color: $title;
	@include font(13, 16);
}

/* ---------- */

/* Вторая строчка футера */

.ft-row {
	border-top: 1px solid $border-input;
}

.footer__cprt {
	margin-top: rem(10);
	margin-bottom: rem(10);
	@include col-12;
	text-align: center;

	p {
		color: $text-light2;
		@include font(9, 14);
	}

	@include tabletb {
		margin-top: rem(40);
		margin-bottom: rem(40);
	}
}

/* ---------- */

