// БЛОК «ПЕРВЫЙ ЭКРАН»

/* Основа */

.bnr {
	overflow: hidden;
}

/* ---------- */

/* Левая часть первого экрана */

.banner__left {
	position: relative;
	display: flex;
	align-items: flex-start;
	flex-direction: column;
	@include col-12;

	& > * {
		margin-bottom: rem(24);
	}

	&::after {
		position: absolute;
		z-index: -1;
		top: rem(-100);
		left: 0;
		content: "";
		width: rem(400);
		height: rem(400);
		background-image: url("../img/pic/bg/bg-circle.png");
		background-repeat: no-repeat;
		background-size: contain;
		background-position: center;
	}

	@include smart {
		@include col-7;
	}
}

.banner__quote {
	display: flex;
	flex-direction: row;
	max-width: rem(543);
	.ic {
		position: relative;
		top: rem(16);
		width: rem(60);
		height: rem(2);
		background-color: $title;
		margin-right: rem(16);
		@include laptop {
			width: rem(100);
		}
	}
	p {
		@include font(16, 32);
		font-weight: 700;
		color: $title;
	}
}

.bnr-btn {
	margin-top: rem(16);
	width: auto !important;
	@include smart {
		font-size: rem(24);
	}
}

/* ---------- */

/* Правая часть первого экрана */

.banner__right {
	position: relative;
	@include col-12;
	@include smart {
		@include col-5;
	}
}

.bnr-pic-wr {
	@include laptop {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 0;
		width: rem(666);
		height: rem(705);
	}
}

/* ---------- */

// КОНЕЦ БЛОКА «ПЕРВЫЙ ЭКРАН» ==========
