// БЛОК «news»

/* Основа */

.b6-title {
	text-align: center;
}

/* ---------- */

/* Название блока */

.news-card {
	display: flex;
	flex-direction: column;
	.pic {
		margin-bottom: rem(8);
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.news-desc {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: space-between;
	margin-bottom: rem(10);

	p,
	span {
		@include font(9, 14);
	}
}

.tns-nav {
	margin-top: rem(16);
	text-align: center;
	button {
		width: rem(24) !important;
		height: rem(2) !important;
		outline: none !important;
		background-color: $text-light2;
		border: none;
		margin-left: rem(4);
		margin-right: rem(4);
	}
	.tns-nav-active {
		background-color: $btn;
	}
}

