// БЛОК «reviews»

/* Основа */

.b7-title {
	@include tabletb {
		display: flex;
		flex-direction: row;
		align-items: center;
		h2 {
			@include col-7;
			padding-right: rem(64);
		}
		span {
			@include col-5;
		}
	}
}

/* ---------- */

/* Название блока */

.reviews-slider-wr {
	position: relative;
}

.review-item {
	display: flex;
	flex-direction: column;

	@include tabletb {
		pading-left: rem(40);
		pading-right: rem(40);
	}

	@include laptop {
		padding-left: rem(120);
		padding-right: rem(120);
	}
}

.review__rating {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: rem(8);

	.stars {
		display: flex;
		flex-direction: row;
		align-items: center;
		margin-right: rem(10);

		& > * {
			margin-right: rem(6);
			&:last-child {
				margin-right: 0;
			}
		}
	}
}

.review__text {
	margin-bottom: rem(24);
	p {
		@include font(16, 40);
		font-weight: 700;
		color: $title;
	}
}

.review__user {
	display: flex;
	flex-direction: row;
	align-items: center;
	.ic {
		min-width: rem(32);
		min-height: rem(32);
		max-width: rem(32);
		max-height: rem(32);
		border-radius: 50%;
		margin-right: rem(8);
		overflow: hidden;
	}
	.user-name {
		display: flex;
		flex-direction: column;
		p {
			@include font(14, 18);
			font-weight: 600;
			color: $title;
		}
		span {
			@include font(12, 16);
		}
	}
}

.reviews-btns {
	display: flex;
	flex-direction: row;
	gap: rem(8);
	margin-top: rem(24);

	.arr-btn {
		display: flex;
		align-items: center;
		justify-content: center;
		min-width: rem(32);
		min-height: rem(32);
		max-width: rem(32);
		max-height: rem(32);
		border: rem(1) solid $border-input;
		background-color: $white;
		border-radius: 50%;
		transition: background-color 0.2s ease-in-out;
		cursor: pointer;

		&:hover {
			background-color: $btn-hvr;
		}

		@include smart {
			min-width: rem(48);
			min-height: rem(48);
			max-width: rem(48);
			max-height: rem(48);
		}
	}

	.ic {
		display: flex;
		align-items: center;
		justify-content: center;
		min-width: rem(16);
		min-height: rem(16);
		max-width: rem(16);
		max-height: rem(16);

		svg {
			width: rem(14);
			height: rem(14);
			fill: $border-input2;
		}
	}

	@include smart {
		position: absolute;
		z-index: 1;
		right: 0;
		bottom: 0;
	}
}

.arr-btn-l {
	transform: rotate(180deg);
}

