// КНОПКА

/* База кнопки */

@mixin button($bg: $first, $borderColor: $second) {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
	padding: em(16) em(32) em(17) em(32);
	box-shadow: none;
	font-size: rem(13);
	font-family: inter, sans-serif;
	font-weight: 700;
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	outline: none;
	white-space: nowrap;
	border-radius: em(32);
	border: $borderColor;
	color: $white;
	cursor: pointer;
	background: $bg;
	@content;
}

/* ---------- */

/* Основная кнопка */

// linear-gradient(to right, #F17519 50%, #FFAF13 100%)
.main-btn {
	@include button($btn, transparent);
	transition: background 0.2s ease-in-out;

	&:hover {
		color: $white;
		background-color: $btn-hvr;
	}

	&:active {
		background-color: $btn-act;
	}
}

/* ---------- */

/* Кнопка контур */

.border-btn {
	@include button(transparent, $btn);
	border-width: rem(1);
	border-style: solid;
	color: $btn;
	transition: background 0.2s ease-in-out, color 0.2s ease-in-out;

	&:hover {
		color: $white;
		background-color: $btn;
		box-shadow: 0 em(4) em(4) rgba(248, 151, 24, 0.46);
	}

	&:active {
		background-color: darken($btn, 10%);
	}
}

/* ---------- */

/* Мерцающая точка на кноке */

@keyframes blink {
	from {
		opacity: 1;
	}
	to {
		opacity: 0;
	}
}

.blink {
	&:before {
		animation: blink 1.1s infinite;
	}
}

/* ---------- */

// КОНЕЦ КНОПКИ ==========
