// ШАПКА

/* Основа */

// :root {
// --header-height: rem(80);
// }

.header {
	width: 100%;
	transition: background 0.2s ease-in-out;
}

.header__container {
	padding-top: rem(4);
	padding-bottom: rem(4);

	@include smart {
		padding-top: rem(8);
		padding-bottom: rem(8);
	}

	@include tabletm {
		padding-top: rem(24);
		padding-bottom: rem(24);
	}
}

.header--fixed {
	position: fixed;
	z-index: 40;
	top: 0;
	left: 0;
	background-color: rgba(255, 255, 255, 0.95);
	@include shd-l;

	.header__container {
		@include tabletm {
			padding-top: rem(16);
			padding-bottom: rem(16);
		}
	}
}

.b-p {
	padding-top: rem(48);

	@include smart {
		padding-top: rem(57);
	}

	@include tabletm {
		padding-top: rem(74);
	}

	@include laptop {
		padding-top: rem(76);
	}
}

/* ---------- */

/* Логотип */

.header__logo {
	display: flex;
	@include col-4;

	a {
		display: flex;
		align-items: center;
		max-width: rem(140);

		&:before {
			display: none;
		}
	}

	@include tabletm {
		@include col-3;
	}

	@include laptop {
		@include col-2;
	}
}

/* ---------- */

/* КОНТАКТЫ И КНОПКА МЕНЮ */

/* Контакты */

.header__contacts {
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
	@include col-8;

	@include tabletm {
		@include col-9;
	}

	@include laptop {
		@include col-10;
	}
}

.header__call-btn {
	display: none;
	// font-size: rem(13);

	&:before {
		position: absolute;
		top: 44%;
		left: 10%;
		content: "";
		width: 0.3em;
		height: 0.3em;
		border-radius: 50%;
		background-color: $white;
	}

	@include smart {
		display: flex;
		margin-right: rem(20);
	}

	@include tabletb {
		font-size: rem(14);
	}
}

/* ---------- */

/* Кнопка меню */

.navbar-toggle {
	display: flex;
	justify-content: flex-end;
	align-items: center;
	height: 100%;
	padding: rem(8);
	border-radius: rem(4);
	transition: background 0.2s ease-in-out, color 0.2s ease-in-out;
	cursor: pointer;

	.navbar-toggler {
		position: relative;
		width: rem(24);
		height: rem(24);
		cursor: pointer;
		transform: rotate(0deg);

		svg {
			opacity: 0.5;
			// transition: 0.2s ease-in-out;
		}
	}

	&:hover {
		background-color: rgba(255, 255, 255, 0.7);

		.burger-btn {
			svg {
				opacity: 1;
				fill: $btn;
			}
		}
	}

	@include smart {
		padding-left: rem(12);
		padding-right: rem(12);
	}
}

