// ИНПУТЫ

/* База ИНПУТОВ */

@mixin input($bg: $first, $borderColor: $second) {
	position: relative;
	display: block;
	width: 100%;
	padding: em(16);
	border: none;
	border-bottom: rem(1) solid $borderColor;
	font-family: inter, Roboto, sans-serif;
	font-size: rem(13);
	height: 100%;
	appearance: none;
	cursor: text;
	outline: none;
	box-shadow: none;
	line-height: normal;
	background-color: $bg;
	transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;

	&::placeholder {
		color: $text;
	}

	@content;
}

/* ---------- */

/* Вид основных инпутов */

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="password"] {
	@include input($input-bg, $border-input);
	position: relative;

	&:hover {
		border-bottom: rem(1) solid lighten($btn, 35%);
	}

	&:focus {
		border-bottom: rem(1) solid $btn;
	}
}

.input-box {
	position: relative;
	text-align: right;
	width: 100%;
	height: rem(40);
}

