// НАЗВАНИЕ БЛОКА

/* Основа */

.b2-sct {
	background-color: $bg-dark;
}

/* ---------- */

/* Блок */

.pop-info-btn {
	max-width: 300px;
}

.main-title {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(40);

	h2 {
		margin-bottom: rem(24);
	}
	p {
		@include font(14, 18);
	}

	@include tabletb {
		margin-bottom: rem(80);
	}
}

.b2-title {
	@include col-12;

	h2 {
		color: $white;
	}

	@include smart {
		@include col-8;
	}
}

.advantages__items {
	display: flex;
	flex-direction: column;
	gap: rem(16);

	& > * {
		&.advantage:last-child p {
			margin-bottom: 0;
		}
	}

	@include smart {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: rem(40);
	}
	@include laptop {
		gap: rem(112);
	}
}

.advantage {
	display: flex;
	flex-direction: row;

	.ic {
		position: relative;
		top: rem(4);
		display: flex;
		align-items: center;
		justify-content: center;
		min-width: rem(24);
		min-height: rem(24);
		max-width: rem(24);
		max-height: rem(24);
		margin-right: rem(16);

		img {
			width: 100%;
			height: 100%;
		}
	}
}

.advantage__text {
	display: flex;
	flex-direction: column;

	h4 {
		color: $white;
		margin-bottom: rem(8);
	}

	p {
		@include font(11, 16);
	}
}

/* ---------- */

